<template>
    <div class="search-container">
        <nav>
            <input type="text" placeholder="Введите название проекта" v-model="searchProject">
        </nav>
        <div class="search-projects">
            <h1 v-for="project in getProjects" :key="project.id"
            @click="$router.push({name : 'project', params : {name : project.name}})"
            style="font-size : 20px;">
            {{ project.name }} <span v-show="project.isNew" class="new-project" style="font-size : 15px;">new</span></h1>
        </div>
        <nav-footer></nav-footer>
    </div>
</template>

<script>
import Nav from './Nav.vue';

export default {
    data()
    {
        return {
            searchProject : ''
        }
    },
    components : {
        'nav-footer' : Nav
    },
    computed : {
        getProjects()
        {
            return this.$store.getters.getProjects.filter(project => project.name.toUpperCase().includes(this.searchProject.toUpperCase()));
        }
    }
}


</script>