<template>
    <div class="messages-container">
        <header>
            <button class="back-button" @click="$router.back()"><i class="fas fa-arrow-left"></i></button>
            <h1 @click="$router.push({name : 'recipient', params : {login : getRecipient.login ?? 'admin'}})">
                {{ getRecipient?.login?.substring(0, 1).toUpperCase() ?? 'A' }}
            </h1>
            <h2>{{ getRecipient?.login ?? 'Admin'}}</h2>
        </header>
        <div class="messages">
            <div class="message" v-for="item in getSortMessages" :key="item.id"
            :class="{'main-message' : isMainMessage(item)}">{{ item.message }}</div>
        </div>
        <div class="form-message" v-if="getRecipient">
            <textarea placeholder="Сообщение" class="message-data-form" 
            style="align-self : flex-end; margin : 0; min-height : 80px; font-size : 15px;"
            v-model="message"></textarea>
            <button class="green-button message-submit" style="align-self : flex-end;" @click="onMessage"
            :disabled="!message.length" :class="{'dis-button' : !message.length}">отправить</button>
        </div>
        <not-found v-else></not-found>
    </div>
</template>

<script>
import data from './messages';
import NotFound from './404.vue';

export default {
    setup()
    {
        return {...data()}
    },
    components : {
        'not-found' : NotFound
    }
}


</script>