<template>
    <div>
        <app-user :getUser="getRecipient"></app-user>
    </div>
</template>

<script>
import userForms from './userForms.vue';

export default {
    computed : {
        getRecipient()
        {
            const login = this.$route.params.login;
            if (login === 'admin') {return this.$store.getters.getUsers.find(user => !user.login)}
            return this.$store.getters.getUsers.find(user => user.login === login);
        }
    },
    components : {
        'app-user' : userForms
    }
}

</script>
