import { HOST } from './ws';
import axios from 'axios';
import { useStore } from 'vuex';
import { ref, computed } from 'vue';
import querystring from './querystring';
import { useRoute } from 'vue-router';

export default () =>
{
    const store = ref(useStore());
    const route = ref(useRoute());

    const message = ref('');

    const getUser = computed(() => store.value.getters.getUser);

    const getRecipient = computed(() => store.value.getters.getUsers.find(user => user.login === route.value.params.user)
     ?? store.value.getters.getUsers.find(user => !user.login));

     const getMessages = computed(() => {
         if (getUser.value.status === 'admin') {
             return store.value.getters.getMessages.filter(message => {
                 const user = message.to ?? message.from;
                 return user === route.value.params.user;
             })
         }
         return store.value.getters.getMessages.filter(message =>
        {
            const user = message.from ?? message.to;
            return user === getUser.value.login;
        })
     })

     const getSortMessages = computed(() => getMessages.value.sort((a, b) => new Date(a.date) < new Date(b.date) ? -1 : 1));

    const onMessage = () =>
    {
        const body = {
            message : message.value,
            from : getUser.value.status === 'admin'? '' : getUser.value.login,
            to : getUser.value.status === 'admin'? route.value.params.user : ''
        }

        axios({
            method: 'POST',
            headers: { 'content-type': 'application/x-www-form-urlencoded' },
            data : querystring(body),
            url: `http${HOST}/messages/add`
        })

        store.value.commit('addMessage', body);

        message.value = '';
    }

    const isMainMessage = message =>
    {
        if (getUser.value.status === 'admin') {
            return message.to;
        }
        return message.from;
    }

    return {
        message,
        onMessage,
        getRecipient,
        getSortMessages,
        isMainMessage
    }
}