const HOST = 's://portfolio1518.herokuapp.com';

let ws = new WebSocket(`ws${HOST}`);

let updaters = new Map();

const setUpdater = (name, cb) =>
{
    updaters.set(name, [cb]);
}

const onMessage = message =>
{
    const {data, method} = JSON.parse(message.data);
    if (updaters.get(method)) {
        updaters.get(method).map(cb => cb(data));
    }
}

const setUpdaters = commit =>
{
    updaters = new Map();
    setUpdater('add-project', projects => commit('addProjects', projects));
    setUpdater('remove-project', data => commit('removeProject', data.name));
    setUpdater('add-grade', grade => commit('addGrade', grade));
    setUpdater('delete-grade', login => commit('deleteGrade', login));
    setUpdater('add-message', message => commit('updateMessages', message));
    setUpdater('delete-messages', data => commit('deleteMessages', data.login));
}

const loadData = (commit, dispatch, user) =>
{
    commit('updateUser', user);
    dispatch('getProjects');
    dispatch('getGrades');
    dispatch('getUsers', user.status);
    dispatch('getMessages', {status : user.status, login : user.login})
}

ws.addEventListener('message', onMessage);

export { HOST, setUpdaters, loadData }