<template>
    <transition name="popup-container">
        <div class="popup-container" v-show="isOpen">
            <transition name="popup-body">
                <div class="popup-body" v-show="isBodyVisible">
                    <h2 style="color : black;">Передвиньте ползунок. Ваша оценка : {{ value }} / 100</h2>
                    <input type="range" min="0" max="100" v-model="value" style="padding : 0" @keydown.enter="addValue">
                    <div class="popup-footer">
                        <button class="green-button red-button" @click="errorClose">отмена</button>
                        <button class="green-button" @click="addValue">оценить</button>
                    </div>
                </div>
            </transition>
    </div>
    </transition>
</template>

<script>
export default {
    resolve : null,
    reject : null,
    data()
    {
        return {
            value : 50,
            isOpen : false,
            isBodyVisible : false
        }
    },
    methods : {
        open()
        {
            this.isOpen = true;
            this.isBodyVisible = true;
            return new Promise((resolve, reject) =>
            {
                this.$options.resolve = resolve;
                this.$options.reject = reject;
            })
        },
        close()
        {
            this.isBodyVisible = false;
            this.isOpen = false;
        },
        addValue()
        {
            this.close();
            this.$options.resolve(this.value);
        },
        errorClose()
        {
            this.close();
            this.$options.reject();
        }
    }
}
</script>