import { ref, computed } from 'vue';
import { HOST } from './ws';
import { useStore } from 'vuex';
import axios from 'axios';
import querystring from './querystring';

export default () =>
{
    const name = ref('');
    const url = ref('');
    const description = ref('');
    const error = ref(false);
    const nameError = ref(false);
    const imagePath = ref('');

    const store = ref(useStore());

    const getProjectsName = computed(() => store.value.getters.getProjects.map(project => project.name));

    const addProject = () =>
    {
        if (!(name.value && url.value && description.value)) {
            error.value = true;
            return;
        }

        if (getProjectsName.value.indexOf(name.value) !== -1) {
            nameError.value = true;
            return;
        }

        const body = {name : name.value, url : url.value, description : description.value, imagePath : imagePath.value};

        axios({
            method: 'POST',
            headers: { 'content-type': 'application/x-www-form-urlencoded' },
            data : querystring(body),
            url: `http${HOST}/project`
        })
        
        clearForms();
        error.value = false;
        nameError.value = false;
    }

    const clearForms = () =>
    {
        name.value = '';
        url.value = '';
        description.value = '';
        imagePath.value = '';
    }

    return {
        name,
        url,
        description,
        HOST,
        addProject,
        clearForms,
        error,
        nameError,
        imagePath
    }
}