<template>
    <div class="spiner-container">
        <canvas ref="canvas"></canvas>
    </div>
</template>

<script>
export default {
    data()
    {
        return {
            ctx : null,
            point : 0,
            x : 100,
            y : 100
        }
    },
    mounted()
    {
        this.ctx = this.$refs.canvas.getContext('2d');
        this.ctx.strokeStyle = 'white';
        this.ctx.lineWidth = 4;
        this.ctx.fillStyle = 'black';
        this.x = this.$refs.canvas.width / 2;
        this.y = this.$refs.canvas.height / 2;
        requestAnimationFrame(this.update);
    },
    methods : {
        draw()
        {
            this.ctx.fillRect(0, 0, this.$refs.canvas.width, this.$refs.canvas.height);
            this.ctx.fill();
            this.ctx.beginPath();
            this.ctx.arc(this.x, this.y, 25, this.point, this.point + 3 * Math.PI / 1.8);
            this.ctx.stroke();
        },
        updatePoint()
        {
            this.point = this.point > Math.PI * 2 ? 0 : this.point + Math.PI / 20;
        },
        update()
        {
            this.updatePoint();
            this.draw();
            requestAnimationFrame(this.update);
        }
    }
}
</script>