<template>
    <div class="search-container">
        <nav><input type="text" placeholder="Поиск" v-model="findChat"></nav>
        <div class="search-projects">
            <h1 style="font-size : 20px;" v-for="chat in getChats" :key="chat"
             @click="$router.push({name : 'messages', params : {user : chat}})">{{ chat }} 
             <button class="green-button" @click.stop="deleteMessages(chat)">очистить</button></h1>
        </div>
        <nav-footer></nav-footer>
    </div>
</template>

<script>
import Nav from './Nav.vue';
import axios from 'axios';
import { HOST } from './ws';

export default {
    data()
    {
        return {
            findChat : ''
        }
    },
    components : {
        'nav-footer' : Nav
    },
    computed : {
        getChats()
        {
            return [...new Set(this.$store.getters.getMessages.map(message => (message.from !== '' && message.from) ? message.from : message.to))]
            .filter(chat => chat.toUpperCase().includes(this.findChat.toUpperCase()) && chat !== this.$store.getters.getUser.login)
            .reverse();
        }
    },
    methods : {
        deleteMessages(chat)
        {
            axios({
                url : `http${HOST}/messages`,
                method : 'DELETE',
                headers: { 'content-type': 'application/x-www-form-urlencoded' },
                data : `chat=${chat}`
            })
        }
    }
}
</script>