import { ref, computed } from 'vue';
import { useStore } from 'vuex';
import { useRouter } from 'vue-router';
import axios from 'axios';
import { HOST } from './ws';

export default () =>
{
    const store = ref(useStore());
    const router = ref(useRouter());

    const getUser = computed(() => store.value.getters.getUser);

    const exit = () =>
    {
        localStorage.clear();
        router.value.push({name : 'home'});
    }

    const deleteUser = () =>
    {
        axios({
            url : `http${HOST}/user`,
            method : 'DELETE',
            headers: { 'content-type': 'application/x-www-form-urlencoded' },
            data : `login=${getUser.value.login}`
        }).then(res =>
        {
            if (res.data.valid) {
                localStorage.clear();
                router.value.push({name : 'home'});
            }
        })
    }

    return {
        getUser,
        exit,
        deleteUser
    }
}