<template>
    <div class="projects-container">
        <div class="project" v-for="project in $store.getters.getProjects" :key="project.id"
        @click="$router.push({name : 'project', params : {name : project.name}})">
            <h1>{{ project.name.length > 20? `${project.name.substring(0, 21)}...` : project.name }}</h1>
            <img :src="project.imagePath">
            <p>{{ project.description.length > 173? `${project.description.substring(0, 174)}...` : project.description }}</p>
            <span class="new-project" style="position : absolute; right : 10px; top : 10px;" v-show="project.isNew">new</span>
        </div>
        <nav-footer></nav-footer>
    </div>
</template>

<script>
import { HOST } from './ws';
import Nav from './Nav.vue';

export default {
    setup()
    {
        return { HOST }
    },
    components : {
        'nav-footer' : Nav
    }
}


</script>