<template>
    <div class="project-container">
        <div v-if="getProject" class="data-project">
            <h1>{{ $route.params.name }}</h1>
            <img :src="getProject.imagePath" v-load v-once>
            <p>{{ getProject.description }}</p>
            <nav>
                <div class="graph">
                    <div class="graph-value" :style="{height : `${getGradesValue}%`}" v-color:[getGradesValue]></div>
                    <div class="graph-value-data">{{ getGradesValue }}</div>
                </div>
                <ul>
                    <li>Всего отзывов: {{ $store.getters.getGrades.filter(grade => grade.name === getProject.name).length }}</li>
                    <li>Средняя оценка сайта: {{ getGradesValue }}</li>
                    <a :href="getProject.url" target="_blank"><button class="green-button">Смотреть сайт</button></a>
                    <button class="green-button" @click="openPopup">Оставить отзыв</button>
                </ul>
            </nav>
            <footer>
                <button class="green-button red-button" @click="removeProject" :disabled="wait" :class="{'wait' : wait}"
                v-if="$store.getters.getUser?.status === 'admin'">удалить</button>
            </footer>
        </div>
        <not-found v-else></not-found>
        <nav-footer></nav-footer>
        <app-popup ref="popup"></app-popup>
    </div>
</template>

<script>
import data from './project';
import NotFound from './404.vue';
import Nav from './Nav.vue';
import Popup from './Popup.vue';

export default {
    setup()
    {
        return { ...data() }
    },
    components : {
        'not-found' : NotFound,
        'nav-footer' : Nav,
        'app-popup' : Popup
    },
    methods : {
        async openPopup()
        {
            await this.$refs.popup.open()
            .then(this.addGrade)
            .catch(() => {return;});
        }
    }
}


</script>