<template>
    <div class="container-add-project">
        <input type="text" placeholder="Название проекта" v-model="name" :class="{'error' : (error && !name) || nameError}">
        <input type="text" placeholder="Ссылка на проект" v-model="url" :class="{'error' : error && !url}">
        <textarea placeholder="Описание проекта" v-model="description" :class="{'error' : error && !description}"></textarea>
        <input type="text" placeholder="Путь до картинки" v-model="imagePath">
        <div class="add-project-footer">
            <button class="green-button red-button" @click="clearForms">очистить</button>
            <button class="green-button" @click="addProject">добавить</button>
        </div>
    </div>
</template>

<script>
import data from './AddProject';

export default {
    setup()
    {
        return {...data()}
    },
    watch : {
        'name'()
        {
            this.nameError = false;
        }
    }
}


</script>