<template>
    <div class="nav-footer">
        <button :class="{'active' : getPathParams === 'projects'}" @click="$router.push({name : 'projects'})"><i class="far fa-folder"></i></button>
        <button :class="{'active' : getPathParams === 'messages'}" @click="toMessages"><i class="far fa-comment"></i></button>
        <button :class="{'active' : getPathParams === 'search'}" @click="$router.push({name : 'search'})"><i class="fas fa-search"></i></button>
        <button :class="{'active' : getPathParams === 'user'}" @click="$router.push({name : 'user'})"><i class="fas fa-user"></i></button>
    </div>
</template>

<script>
export default {
    computed : {
        getPathParams()
        {
            const path = this.$route.path.split('/')[1];
            return path;
        }
    },
    methods : {
        toMessages()
        {
            const status = this.$store.getters.getUser?.status;
            if (status === 'admin') {this.$router.push({name : 'chats', params : {status : 'admin'}}); return;}
            this.$router.push({name : 'messages', params : {user : 'admin'}});
        }
    }
}
</script>