import { createStore } from 'vuex';
import axios from 'axios';
import { HOST } from '../components/ws';

const differenceDays = projectDate =>
{
  const pD = new Date(projectDate);
  const date = new Date();
  return ((date - pD) / 3600000 / 24) < 3;
}

export default createStore({
  state: {
    projects : [],
    user : {},
    grades : [],
    users : [],
    messages : [],
    load : false
  },
  mutations: {
    addProjects(state, projects)
    {
      if (Array.isArray(projects)) {
        state.projects = [...projects];
        return;
      }
      state.projects.unshift(projects);
    },
    removeProject(state, name)
    {
      state.projects = state.projects.filter(project => project.name !== name);
    },
    updateUser(state, newUser)
    {
      state.user = newUser;
    },
    updateDates(state)
    {
      state.projects = state.projects.map(project => {return {...project, isNew : differenceDays(project.date)}});
    },
    addGrade(state, object)
    {
      state.grades = state.grades.filter(grade => !((grade.login === object.login) && (grade.name === object.name)));
      state.grades.push(object);
    },
    updateGrades(state, data)
    {
      state.grades = [...data];
    },
    deleteGrade(state, login)
    {
      state.grades = state.grades.filter(grade => grade.login !== login);
    },
    updateUsers(state, data)
    {
      if (Array.isArray(data)) {state.users = [...data]; return;}
      if (data.unvalid) {return;}
      state.users.push(data);
    },
    updateMessages(state, data)
    {
      if (Array.isArray(data)) {state.messages = [...data]; return;}
      if (state.user.status === 'admin' && data.from) {state.messages.push(data); return;}
      if (state.user.login === data.to) {state.messages.push(data)}
    },
    addMessage(state, body)
    {
      state.messages.push(body);
    },
    deleteMessages(state, login)
    {
      state.messages = state.messages.filter(message => message.from !== login && message.to !== login);
    },
    spinnerUpdate(state, value)
    {
      state.load = value;
    }
  },
  actions: {
    getProjects(ctx)
    {
      axios.get(`http${HOST}/projects`).then(res =>
      {
        ctx.commit('addProjects', res.data);
      })
    },
    updateDates(ctx)
    {
      ctx.commit('updateDates');
      setInterval(() => ctx.commit('updateDates'), 1000);
    },
    getGrades(ctx)
    {
      axios.get(`http${HOST}/grades`).then(res => {
        ctx.commit('updateGrades', res.data);
      })
    },
    getUsers(ctx, status)
    {
      axios.get(`http${HOST}/getUsers/${status}`).then(res => ctx.commit('updateUsers', res.data));
    },
    getMessages(ctx, userParams)
    {
      ctx.commit('spinnerUpdate', false);
      axios.get(`http${HOST}/messages/${userParams.login}/${userParams.status}`).then(res => ctx.commit('updateMessages', res.data));
    }
  },
  getters: {
    getProjects(state)
    {
      return state.projects;
    },
    getUser(state)
    {
      return state.user;
    },
    getGrades(state)
    {
      return state.grades;
    },
    getMessages(state)
    {
      return state.messages;
    },
    getUsers(state)
    {
      return state.users;
    },
    upload(state)
    {
      return state.load;
    }
  }
})
