import { createRouter, createWebHashHistory } from 'vue-router'
import Home from '../components/Home.vue';
import AddProject from '../components/AddProject.vue';
import Projects from '../components/Projects.vue';
import Project from '../components/Project.vue';
import Log from '../components/Log in.vue';
import Sign from '../components/Sign in.vue';
import NotFound from '../components/404.vue';
import Messages from '../components/Messages.vue';
import Search from '../components/Search.vue';
import User from '../components/User.vue';
import Chats from '../components/Chats.vue';
import Recipient from '../components/Recipient.vue';
import { getStorage } from '../components/storage';

const routes = [
  {path : '/', component : Home, name : 'home'},
  {path : '/projects/add', component : AddProject, name : 'add-project'},
  {path : '/projects', component : Projects, name : 'projects'},
  {path : '/:pathMatch(.*)*', component : NotFound},
  {path : '/projects/project/:name', component : Project, name : 'project'},
  {path : '/login', component : Log, name : 'log in'},
  {path : '/signin', component : Sign, name : 'sign in'},
  {path : '/messages/:user', component : Messages, name : 'messages'},
  {path : '/search', component : Search, name : 'search'},
  {path : '/user', component : User, name : 'user'},
  {path : '/messages', component : Chats, name : 'chats'},
  {path : '/recipient/:login', component : Recipient, name : 'recipient'}
]

const router = createRouter({
  history: createWebHashHistory(),
  routes
})

router.beforeEach(to =>
{
  if (to.path !== '/' && to.path !== '/signin' && to.path !== '/login') {
    if (!getStorage()) {return false;}
  }

  if (to.path === '/projects/add') {
    if (getStorage()?.status === 'admin') {
      return true;
    }
    return false;
  }
})

export default router
