import { HOST } from './ws';
import { ref, computed } from 'vue';
import { useStore } from 'vuex';
import { useRoute } from 'vue-router';
import axios from 'axios';
import querystring from './querystring';

export default () =>
{
    const store = ref(useStore());
    const route = ref(useRoute());

    const wait = ref(false);

    const getProject = computed(() => store.value.getters.getProjects.find(project => project.name === route.value.params.name));

    const removeProject = () =>
    {
        wait.value = true;
        axios({
            url : `http${HOST}/project`,
            method : 'DELETE',
            headers: { 'content-type': 'application/x-www-form-urlencoded' },
            data : `name=${route.value.params.name}`
        })
    }

    const addGrade = value =>
    {
       axios({
           method : 'POST',
           url : `http${HOST}/grades`,
           headers: { 'content-type': 'application/x-www-form-urlencoded' },
           data : querystring({login : store.value.getters.getUser?.login, value, name : getProject.value.name})
       });
       store.value.commit('addGrade', {login : store.value.getters.getUser?.login, value, name : getProject.value.name});
    }

    const getGradesValue = computed(() => {
        let sum = 0;
        const grades = store.value.getters.getGrades.filter(grade => grade.name === getProject.value.name)
        .map(grade => {return {...grade, value : +grade.value}});
        if (!grades.length) {return 0}
        grades.map(grade => sum+=grade.value);
        return (sum / grades.length).toFixed(2);
    })

    return {
        HOST,
        getProject,
        removeProject,
        wait,
        addGrade,
        getGradesValue
    }
}