import { onMounted, ref } from 'vue';
import { useStore } from 'vuex';
import { useRouter, useRoute } from 'vue-router';
import { getStorage } from './storage';
import axios from 'axios';
import { HOST, setUpdaters, loadData } from './ws';

export default () =>
{
    const store = ref(useStore());
    const router = ref(useRouter());
    const route = ref(useRoute());

    onMounted(() =>
    {
        store.value.dispatch('updateDates');
        const hasUser = getStorage();
        if (hasUser) {
            store.value.commit('spinnerUpdate', true);
            axios.get(`http${HOST}/users/${hasUser?.login}/${hasUser?.password}`).then(res =>
            {
                if (res.data.unvalidPassword || res.data.unvalidLogin) {
                    localStorage.clear();
                    switch (route.value.path) {
                        case '/' :
                        case '/login':
                        case '/signin' : break;
                        default : router.value.push('/');
                    }
                    store.value.commit('spinnerUpdate', false);
                    return;
                }
                loadData(store.value.commit, store.value.dispatch, res.data);
                setUpdaters(store.value.commit);
                switch (route.value.path) {
                    case '/':
                    case '/login':
                    case '/signin': router.value.push({name : 'projects'}); break;
                    default : return;
                }
            })
        } else {
            switch (route.value.path) {
                case '/' :
                case '/login':
                case '/signin' : break;
                default : router.value.push('/');
            }
        }
    })

    return {
        
    }
}